function Graph ({ events }) {
  
  // map through genres
  const data = genres.map(genre => {
    // map through event props
    
    if(events.length === 0){
      return { name: 'no value', value: 1 }
    }
    const value = events.filter(event => {
      const regex = /[!"#$%&'()*+,-./:;<=>?@[\]^_`{|}~]/g
      const removedPunctuation = event.summary.replace(regex, ' ')
      const summary = removedPunctuation.split(' ')
      if (summary.includes(genre)) {
        return true
      }
      return false
      // .length specifiy the number of events
    }).length 
    return { name: genre, value }
  })
  
  return (
    <PieChart className='chart__pie' width={400} height={400}>
      <Pie
        // activeIndex={1}
        // activeShape={text}
        // onMouseEnter={CustomTooltip}
        isAnimationActive={false}
        data={data}
        cx={200}
        cy={200}
        labelLine={false}
        label={renderCustomizedLabel}
        outerRadius={190}
        innerRadius={50}
        fill='#8884d8'
        dataKey='value'
        nameKey='name'
        legendType='circle'
      >
        {/* <Tooltip content={<CustomTooltip />} /> */}
        
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
        
        <Label
          value={events.length}
          position="center"
          fill="#eaf8bf"
          style={{
            fontSize: "32px",
            fontWeight: "bold",
          }}
        />
      </Pie>
    </PieChart>
  )
}
export default Graph
